.catalog-item__content .swiper-pagination-bullet,
.catalog-item__content .swiper-pagination-bullet-active {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 521px 497px;
}

.catalog-item__icon-bar > div {
  margin-right: 25px;
}

.catalog-item__content {
  display: flex;
  flex-flow: column;
}

.catalog-item__content .swiper-pagination-bullet {
  width: 11px;
  height: 11px;
  background-position: -268px -121px;
  border: none;
  transform: none;
  margin: 0;
  border-radius: 0;
  background-color: transparent;
  opacity: 1;
}

.catalog-item__content .swiper-pagination-bullet-active {
  width: 11px;
  height: 11px;
  background-position: -268px -101px;
  border: none;
  margin: 0;
  border-radius: 0;
  background-color: transparent;
  opacity: 1;
}

.catalog-item__header {
  grid-column: span 3;
  grid-area: hd;
  background: #fff;
  padding: 35px 35px 40px;
}

.catalog-item__header .catalog-item__title h1 {
  margin: 0;
  font-size: 32px;
  line-height: 40px;
  margin-bottom: 20px;
}

.catalog-item__icon-bar {
  display: flex;
}

.catalog-item__image {
  background: #fff;
  padding-top: 50px;
}

.catalog-item__image .to-favorite {
  display: none;
}

.catalog-item__labels {
  position: absolute;
  width: 300px;
  display: flex;
  top: 21px;
  z-index: 10;
}

.catalog-item__features {
  display: flex;
  flex-flow: row nowrap;
  background: #fff;
  padding: 40px;
  grid-area: more;
  align-items: center;
  order: 3;
}

.catalog-item__features .item-features-product {
  flex: 1 50%;
  margin-bottom: 20px;
}

.catalog-item__features .item-features-product:last-child {
  margin-bottom: 0;
}

.catalog-item__price {
  background: #fff;
  grid-area: price;
  padding: 18px 18px 40px;
  order: 1;
}

.catalog-item__price .catalog-item__row {
  display: flex;
  height: 40px;
}

.catalog-item__price .btn_arrow-right {
  display: none;
}

.catalog-item__price span {
  font-size: 15px;
}

.catalog-item__price > *:first-child {
  margin-top: 0;
}

.catalog-item__price > *:last-child {
  margin-bottom: 0;
}

.catalog-item__price-value {
  font-size: 40px;
  line-height: 1;
  font-weight: 700;
  color: #333333;
}

.catalog-item__description {
  background: #fff;
  grid-area: desc;
  font-weight: 500;
  padding: 30px 18px;
  order: 2;
}

.catalog-item__description table {
  margin: 0 0 10px;
}

.catalog-item__description > *:first-child {
  margin-top: 0;
}

.catalog-item__description > *:last-child {
  margin-bottom: 0;
}

.btn-price__btn-inline-text {
  display: inline;
  background: #fff;
  color: #333333;
  position: absolute;
  right: 1px;
  top: 1px;
  bottom: 1px;
  border-radius: 3px;
  line-height: 18px;
  padding: 15px;
}

.btn-price {
  padding-right: 113px;
  margin: 25px 0;
  display: block;
  padding-left: 35px;
}

.item-features-product__icon {
  max-width: 44px;
  display: block;
  margin-right: 15px;
}

.item-features-product__link {
  display: flex;
  align-items: center;
  border-bottom: 0;
}

.item-features-product__title {
  /*! padding-right: 15px; */
  display: block;
  /*! margin-right: 15px; */
  line-height: 23px;
  font-size: 15px;
  font-weight: 500;
}

.icon-3d {
  display: none;
}

@media only screen and (min-width: 768px) {
  .catalog-item__content {
    display: grid;
    grid-template-rows: 1fr auto auto 1fr;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-auto-flow: column dense;
    grid-gap: 1px;
    margin-bottom: 80px;
    grid-template-areas: "hd hd" "img desc" "img price" "thumbs more";
  }

  .catalog-item__content:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    display: block;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: #D6D6D6;
  }

  .catalog-item__image {
    grid-row: 2/ span 3;
    grid-area: img;
    display: flex;
    padding-top: 80px;
  }

  .catalog-item__features .item-features-product {
    margin-bottom: 0;
    margin-right: 10px;
  }

  .catalog-item__price .btn_arrow-right {
    display: flex;
    flex: 1;
    margin: 21px 10px;
  }

  .catalog-item__price {
    padding: 30px 40px;
  }

  .catalog-item__description {
    padding: 30px 40px;
  }

  .item-features-product__title {
    font-size: 13px;
    line-height: 18px;
  }

  .icon-3d {
    display: flex;
    align-items: center;
    width: 60px;
    margin-right: 25px;
    align-self: center;
  }
}

@media only screen and (min-width: 1280px) {
  .catalog-item__price {
    padding: 40px 50px 40px;
  }

  .catalog-item__description {
    padding: 40px 50px 40px;
  }
}

@media only screen and (max-width: 1023px) {
  .catalog-item__header .catalog-item__title h1 {
    font-size: 24px;
    line-height: normal;
  }

  .catalog-item__header {
    padding: 35px 20px 40px;
  }
}

@media only screen and (max-width: 767px) {
  .catalog-item__content {
    margin: 0 -18px;
  }

  .catalog-item__content .catalog-item__thumbs .swiper-wrapper {
    display: none;
  }

  .catalog-item__header .catalog-item__title h1 {
    padding-left: 18px;
    padding-right: 18px;
  }

  .catalog-item__header {
    padding: 0;
    background: #f2f2f2;
  }

  .catalog-item__icon-bar .to-share {
    display: none;
  }

  .catalog-item__icon-bar .to-favorite {
    display: none;
  }

  .catalog-item__image .to-favorite {
    display: block;
    position: absolute;
    top: 17px;
    right: 16px;
  }

  .catalog-item__labels {
    top: 14px;
  }

  .catalog-item__features {
    padding: 25px 18px;
    background-color: #EEEEEE;
    display: flex;
    align-items: stretch;
    flex-flow: column;
  }

  .catalog-item__features .item-features-product__link {
    background-color: #fff;
    border: 1px solid #DBDBDB;
    padding: 18px 18px 19px;
  }

  .catalog-item__features .item-features-product__link .item-features-product__icon {
    width: 31px;
    margin: auto 15px auto 0;
  }

  .catalog-item__features .item-features-product__link .item-features-product__title {
    font-size: 13px;
    line-height: 20px;
  }

  .catalog-item__description {
    border-top: 1px solid #D6D6D6;
  }

  .btn-price {
    width: 100%;
    max-width: 360px;
    margin: 25px auto;
  }
}